@keyframes bgcolor {
  0% {
    background-color: rgba(97, 93, 93, 0.3);
  }
  100% {
    background-color: rgba(0, 0, 0, 0.4);
  }
}

@keyframes watch {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes watch {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
